import React, { useState } from "react"
import Layout from '../../containers/layout'
import Calendar from '../../containers/Calendar'
import { format, setMonth } from 'date-fns'
import { Button, Box } from 'grommet';
import { addMonths, subMonths } from 'date-fns'
const Home = () => {
  const [month, setMonth] = useState(new Date())
  return <Layout max="100%">
    <Box direction="row">
      <Button primary label="Previous" onClick={() => setMonth(subMonths(month, 1)) } />
      <Button primary label="Next" onClick={() => setMonth(addMonths(month, 1))} /> 
    </Box>
    <h2>{ format(month, 'yyyy MMM') }  Calendar  </h2>
    <Calendar dateObject={new Date(month.getTime())} />
  </Layout>
}

export default Home
